import React from "react";
import _ from "lodash";
import "./Styles/User.scss";
import { ActionTypes, formatDateString, joinClassNames, UserStatus } from "../Constants";
import UserStore from "./Stores/UserStore";
import Tooltip from "../../Components/Tooltip";
import { User as UserFeather } from "react-feather";

export default class User {
	id = "";
	displayName = "";
	avatarUrl = "";

	constructor(user) {
		_.extend(this, user);
	}

	get AvatarComponent() {
		return ({ size = 40, className = "" }) => {
			const avatarUrl = UserStore.useState(() => this.avatarUrl);
			const displayName = UserStore.useState(() => this.displayName);

			const [failed, setFailed] = React.useState(false);

			return (
				<div
					className={joinClassNames("AvatarContainer StyleSquare FlexCenter", className)}
					style={{ width: size, height: size }}
				>
					{failed
						? (
							<UserFeather
								className="DefaultAvatar"
							/>
						)
						: (
							<img
								className="Avatar"
								src={avatarUrl}
								alt={displayName}
								width={size} height={size}

								onError={() => setFailed(true)}
							/>
						)}
				</div>
			);
		};
	}

	get StatusText() {
		return ({ tooltipDirection = "right" }) => {
			const status = UserStore.useState(() => this.status);
			const lastOnline = this.lastOnline ? new Date(this.lastOnline) : null;

			switch (status) {
				default:
				case UserStatus.OFFLINE: {
					return (
						<div className="Flex StatusContainer">
							<div className="Status Offline" />
							<div>Offline</div>

							{lastOnline && tooltipDirection !== "none" && (
								<Tooltip direction={tooltipDirection}>
									Last online {formatDateString(lastOnline)} at {lastOnline.toLocaleTimeString("en-US")}
								</Tooltip>
							)}
						</div>
					);
				}

				case UserStatus.ONLINE: {
					return (
						<div className="Flex StatusContainer">
							<div className="Status Online" />
							<div>Online</div>
						</div>
					);
				}

				case UserStatus.AWAY: {
					return (
						<div className="Flex StatusContainer">
							<div className="Status Away" />
							<div>Away</div>
						</div>
					);
				}

				case UserStatus.BUSY: {
					return (
						<div className="Flex StatusContainer">
							<div className="Status Busy" />
							<div>Busy</div>
						</div>
					);
				}

				case UserStatus.BOT: {
					return (
						<div className="Flex StatusContainer">
							<div className="Status Bot" />
							<div>Bot</div>
						</div>
					);
				}
			}
		};
	}

	get Mention() {
		return () => {
			const displayName = UserStore.useState(() => this.displayName);

			return (
				<div className="UserMention">
					<this.AvatarComponent size={19} />
					<div className="Username">{displayName}</div>
				</div>
			);
		};
	}
}